import bridge from "@vkontakte/vk-bridge";
import {
  APP_ID,
  APP_IMG_SHARING,
  APP_IMG_SHARING_STORIES,
  APP_IMG_SHARING_WALL_MAIN,
  APP_IMG_SHARING_WALL_EXCHANGE,
  USER_ID,
} from "../constants";

export function sharing(type, openAlert, e, type_img) {
  switch (type) {
    case "share-link":
      shareLink();
      break;
    case "copy-link":
      copyLink(openAlert);
      break;
    case "story":
      story(type_img);
      break;
    case "share-wall":
      share(e, type_img);
      break;
    default:
      break;
  }
}

function shareLink() {
  const link = `https://vk.com/app${APP_ID}#${USER_ID}`;
  bridge.send("VKWebAppShare", {
    link: link,
  });
}

// Копирование в буфер
function copyLink(openAlert) {
  const link = `https://vk.com/app${APP_ID}#${USER_ID}`;
  bridge
    .sendPromise("VKWebAppGetClientVersion")
    .then((result) => {
      console.log(result.platform);
      if (result.platform === "web" || result.platform === "mobile-web") {
        window.navigator.clipboard.writeText(link).then(
          () => {
            console.log("successfully set", link);
            openAlert("Ссылка скопирована");
          },
          () => {
            console.log("write failed", link);
          }
        );
      } else {
        bridge.send("VKWebAppCopyText", { text: link });
        openAlert("Ссылка скопирована");
      }
    })
    .catch((error) => {
      console.log("error", error);
    });
}

//  Поделиться в истории
function story(type_img) {
  const url = `https://vk.com/app${APP_ID}#${USER_ID}`;

  bridge.sendPromise("VKWebAppShowStoryBox", {
    background_type: "image",
    url: type_img === "home" ? APP_IMG_SHARING : APP_IMG_SHARING_STORIES,
    attachment: {
      text: "go_to",
      type: "url",
      url: url,
    },
  });
}

//  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
function share(e, type_img) {
  e.preventDefault();

  const url = `https://vk.com/app${APP_ID}#${USER_ID}`;
  const urlPhotoStories = `${APP_IMG_SHARING_WALL_MAIN},https://vk.com/app${APP_ID}#${USER_ID}`;
  const urlPhotoExchange = `${APP_IMG_SHARING_WALL_EXCHANGE},https://vk.com/app${APP_ID}#${USER_ID}`;
  const textStories = `А ты уже был в ДУРКЕ? Приложение - ${url}`;
  const textExchange = `Купи мои DurkaCoins на бирже, поддержи мою шизу, Приложение - ${url}`;

  bridge.send("VKWebAppShowWallPostBox", {
    message: type_img === "home" ? textStories : textExchange,
    attachments: type_img === "home" ? urlPhotoStories : urlPhotoExchange,
  });
}
