import React from 'react';
import { Panel, PanelHeaderSimple, PanelHeaderBack, Group, Header, Div } from '@vkontakte/vkui';

import { RanksItem } from '../components';

const RankInfo = ({ ranks, go, id }) => {

    const renderRanks = ranks.map(item => {
        return (
            <RanksItem
                key={item.id}
                id={item.id}
                name={item.name}
                ratingMin={item.ratingMin}
                ratingMax={item.ratingMax}
                pillBlue={item.pillBlue}
                pillRed={item.pillRed}
                pillGold={item.pillGold}
                shirt={item.shirt}
                cell={item.cell}
                vacation={item.vacation}
            />
        );
    });

    return (
        <Panel id={id} separator={false}>
            <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
                Звания в ДУРКЕ
            </PanelHeaderSimple>

            <Div>
                <Group header={<Header mode="secondary" aside="Рейтинг">Звание</Header>}>
                </Group>
                {renderRanks}
            </Div>
        </Panel>
    );
};

export { RankInfo };
