import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
} from "@vkontakte/vkui";

const ModalPromo = ({
  setActiveModal,
  onChangeAction,
  activatePromo
}) => {

  const [inputPromo, setInputPromo] = useState("");

  return (
    <>
        <ModalRoot activeModal="modal-promo">
          <ModalCard
            id="modal-promo"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
              />
            }
            header="Активировать ПРОМОКОД"
            caption={
              <>
                <FormLayout>
                    <Input
                      top="Введите ПРОМОКОД"
                      placeholder="DURKA666"
                      onChange={(e) => setInputPromo(e.target.value.trim())}
                      type="text"
                      value={inputPromo}
                    />
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  activatePromo(inputPromo)
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
     
    </>
  );
};

export { ModalPromo };
