import React from "react";
import {
  ModalRoot,
  ModalCard,
  InfoRow,
  FormLayout,
  Input,
  Div,
} from "@vkontakte/vkui";

import { formatCoins } from "../helpers";

const ModalLootBox = ({
  name,
  price,
  icon,
  DC,
  pillBlue,
  pillRed,
  pillGold,
  vacation,
  straitjacket,
  singleСamera,
  targetIdShop,
  setActiveModal,
}) => {
  return (
    <ModalRoot activeModal="info-loot-box">
      <ModalCard
        id="info-loot-box"
        onClose={() => setActiveModal(null)}
        icon={icon}
        header={name}
        caption={
          <>
            {name === "Бронзовая коробка" ? (
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    При покупке может выпасть один из следующих предметов:
                  </InfoRow>
                  <InfoRow style={{ alignItems: "start" }}>
                    1. DurkaCoins - {`${formatCoins(DC)}`} DC
                  </InfoRow>

                  <InfoRow>2. Blue пилюля - {pillBlue} шт</InfoRow>
                  <InfoRow>3. Отпуск - {vacation} шт</InfoRow>
                  <InfoRow>4. Смирительная рубашка - {straitjacket} шт</InfoRow>
                </Div>
              </>
            ) : name === "Серебряная коробка" || name === "Золотая коробка" ? (
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    При покупке может выпасть один из следующих предметов:
                  </InfoRow>
                  <InfoRow style={{ alignItems: "start" }}>
                    1. DurkaCoins - {`${formatCoins(DC)}`} DC
                  </InfoRow>
                  <InfoRow>2. Red пилюля - {pillRed} шт</InfoRow>
                  <InfoRow>3. Отпуск - {vacation} шт</InfoRow>
                  <InfoRow>4. Одиночная камера - {singleСamera} шт</InfoRow>
                </Div>
              </>
            ) : name === "Мистическая коробка" ? (
              <>
                 <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    При покупке может выпасть один из следующих предметов:
                  </InfoRow>
                  <InfoRow style={{ alignItems: "start" }}>
                    1. DurkaCoins - {`${formatCoins(DC)}`} DC
                  </InfoRow>
                  <InfoRow>2. Gold пилюля - {pillGold} шт</InfoRow>
                  <InfoRow>3. Отпуск - {vacation} шт</InfoRow>
                  <InfoRow>4. Одиночная камера - {singleСamera} шт</InfoRow>
                </Div>
              </>
            ) : (
              ""
            )}
          </>
        }
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalLootBox };
