import React from "react";
import {
  ModalRoot,
  ModalCard,
  InfoRow,
  FormLayout,
  Input,
  Div,
  Link
} from "@vkontakte/vkui";

import { formatCoins } from "../helpers";

const ModalStickers = ({
  name,
  price,
  icon,
  DC,
  pillBlue,
  pillRed,
  pillGold,
  vacation,
  straitjacket,
  singleСamera,
  targetIdShop,
  setActiveModal,
  amount
}) => {
  return (
    <ModalRoot activeModal="info-stickers">
      <ModalCard
        id="info-stickers"
        onClose={() => setActiveModal(null)}
        icon={icon}
        header={name}
        caption={
          <>
            {name === "Айлиш"
            || name === "Дурной Попугай"
            || name === "Дурной Птенчик"
            || name === "Джокер"
            || name === "Фильмы"
            || name === "Малибу"
            || name === "Гомер"
            || name === "Оно"
            || name === "Наруто"
            || name === "Время приключений"

             ? (
              <>
                  <InfoRow>
                    При покупке вы получаете эксклюзивные 
                    графические {name} в количестве {amount} шт.
                    После покупки Вам в ЛС придет архив со стикерами и инструкцией.
                    (Перед покупкой необходимо написать любой 
                    текст в ЛС группы <Link href="https://vk.me/durkacoin" target="_blank">
                             {` ДУРКА `} 
                         </Link>
                    )
                  </InfoRow>
              </>
             ) : (
              ""
            )} 
          </>
        }
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalStickers };
