import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import styles from "../styles/custom-styles.sass";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";
import { getRankByRating, formatCoins } from "../helpers";

const ListUsers = ({
  id,
  firstName,
  lastName,
  rating,
  photoURL,
  amount,
  idVk,
  ranks,
  isVIP,
  vipNameColor,
  openMenuVIP,
  type,
}) => {
  const urlUser = "https://vk.com/id" + idVk;

  return (
    <Div className="card">
      <span className="listNumber">{id}</span>

      <Cell
        className="myItem"
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar size={36} src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        // description={}
      >
        <Div
          style={{
            display: "flex",
            justifyContent: "start",
            alignItems: "center",
            flexDirection: "row",
            marginLeft: "15px",
            fontSize: "13px",
          }}
        >
          {isVIP ? (
            <>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  flexDirection: "row",
                  // marginLeft: "15px",
                  fontSize: "13px",
                  width: "100%",
                }}
              >
                <div>
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "start",
                      alignItems: "center",
                      flexDirection: "row",
                    }}
                  >
                    <span
                      style={{
                        color: [, "green", "#D40000", "blue", "#800080"][
                          vipNameColor
                        ],
                      }}
                    >
                      {firstName + " " + lastName}
                    </span>

                    <Icon12Verified
                      onClick={openMenuVIP}
                      style={{ marginLeft: "5px" }}
                      className="animationIcons"
                      fill={
                        [, "green", "#D40000", "blue", "#800080"][vipNameColor]
                      }
                      width={16}
                      height={16}
                    />
                  </div>

                  <Div
                    style={{
                      padding: 0,
                      color: "#818c99",
                    }}
                  >
                    <Div
                      style={{
                        padding: 2,
                        display: "flex",
                        alignItems: "start",
                        flexDirection: "row",
                        // position: "absolute",
                        paddingTop: "-12px",
                      }}
                    >
                      {/* <Icon12Fire fill="red"/> */}
                      <Div className="iconStar" />
                      <Div className="div" style={{ whiteSpace: "nowrap", fontSize: "12px" }}>
                        {rating} ({getRankByRating(rating, ranks)})
                      </Div>
                    </Div>
                    <Div
                      style={{
                        padding: 2,
                        margin: 0,
                        display: "flex",
                        alignItems: "start",
                        flexDirection: "row",
                      }}
                    >
                      {/* <Icon12Fire fill="red"/> */}
                      <Div className="iconDC" />
                      <Div style={{ padding: 0 }}>{formatCoins(amount)}</Div>
                    </Div>
                  </Div>
                </div>
                {type === "general" && id <= 10 ? (
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      flexDirection: "column",
                      width: "70px",
                      height: "40px",
                      backgroundColor: "#3f8ae0",
                      color: "white",
                      borderRadius: "6px",
                      fontSize: "12px",
                    }}
                  >
                    <span>Награда</span>
                    <span>
                      {
                        {
                          1: "+500 ₽",
                          2: "+300 ₽",
                          3: "+200 ₽",
                          4: "GOLD VIP",
                          5: "VIP",
                          6: "+1000 DC",
                          7: "+1000 DC",
                          8: "+1000 DC",
                          9: "+1000 DC",
                          10: "+1000 DC",
                        }[id]
                      }{" "}
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
            </>
          ) : (
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
                flexDirection: "row",
                // marginLeft: "15px",
                fontSize: "13px",
                width: "100%",
              }}
            >
              <div>
                <div>
                  {firstName} {lastName}
                </div>

                <Div
                  style={{
                    padding: 0,
                    color: "#818c99",
                  }}
                >
                  <Div
                    style={{
                      padding: 2,
                      display: "flex",
                      alignItems: "start",
                      flexDirection: "row",
                      // position: "absolute",
                      paddingTop: "-12px",
                    }}
                  >
                    {/* <Icon12Fire fill="red"/> */}
                    <Div className="iconStar" />
                    <Div className="div" style={{ whiteSpace: "nowrap", fontSize: "12px" }}>
                      {rating} ({getRankByRating(rating, ranks)})
                    </Div>
                  </Div>
                  <Div
                    style={{
                      padding: 2,
                      margin: 0,
                      display: "flex",
                      alignItems: "start",
                      flexDirection: "row",
                    }}
                  >
                    {/* <Icon12Fire fill="red"/> */}
                    <Div className="iconDC" />
                    <Div style={{ padding: 0 }}>{formatCoins(amount)}</Div>
                  </Div>
                </Div>
              </div>
              {type === "general" && id <= 10 ? (
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    flexDirection: "column",
                    width: "70px",
                    height: "40px",
                    backgroundColor: "#3f8ae0",
                    color: "white",
                    borderRadius: "6px",
                    fontSize: "12px",
                  }}
                >
                  <span>Награда</span>
                  <span>{
                        {
                          1: "+500 ₽",
                          2: "+300 ₽",
                          3: "+200 ₽",
                          4: "GOLD VIP",
                          5: "VIP",
                          6: "+1000 DC",
                          7: "+1000 DC",
                          8: "+1000 DC",
                          9: "+1000 DC",
                          10: "+1000 DC",
                        }[id]
                      }</span>
                </div>
              ) : (
                ""
              )}
            </div>
          )}
        </Div>
      </Cell>
    </Div>
  );
};

export { ListUsers };
