import React from 'react';
import { ModalRoot, ModalCard, InfoRow, FormLayout, Input,} from '@vkontakte/vkui';

const ModalShopInfoItem = ({iconItem, nameItem, ratingItem, 
  timeItem, targetIdShop, setActiveModal }) => {


    return (
      <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            {targetIdShop >= 1 && targetIdShop <= 3 ? (
              <>
                <InfoRow>
                  {nameItem} - при использовании прибавляет +{ratingItem} к
                  рейтингу.
                </InfoRow>
              </>
            ) : nameItem === "Отпуск" ? (
              <>
                <InfoRow>
                  {nameItem} - предоставляет вам отпуск на {timeItem}, в
                  течение этого времни, никто не может использовать на вас
                  'смирительную рубашку' или 'одиночную камеру'
                </InfoRow>
              </>
            ) : nameItem === "Смирительная рубашка" ? (
              <>
                <InfoRow>
                  {nameItem} - отнимает {ratingItem} рейтинга у любого игрока
                  ДУРКИ
                </InfoRow>
              </>
            ) : nameItem === "Одиночная камера" ? (
              <>
                <InfoRow>
                  {nameItem} - отнимает {ratingItem} рейтинга у любого игрока
                  ДУРКИ.
                </InfoRow>
              </>
            ) : nameItem === "Колпак санитара" ? (
              <>
                <InfoRow>
                  {nameItem} - скорость майнинга {ratingItem} DC/сек.
                </InfoRow>
              </>
            ) : (
              ""
            )}
          </>
        }
      ></ModalCard>
    </ModalRoot>
    );
};

export { ModalShopInfoItem };

