import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
  Div,
} from "@vkontakte/vkui";
import * as hd from "humanize-duration";


const ModalSeason = ({ setActiveModal }) => {

  const nowDate = new Date()
  const stringTime = hd(new Date(nowDate.getFullYear(), nowDate.getMonth()) - Date.now(), {
    language: 'ru',
    units: ['d', 'h', 'm', 's'],
    round: true
    });

  return (
    <>
      <ModalRoot activeModal="modal-season">
        <ModalCard
          id="modal-season"
          onClose={() => setActiveModal(null)}
          icon={
            <Avatar
              size={72}
              src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
            />
          }
          header="Сезоны (beta)"
          caption={
            <Div
              style={{
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
                marginTop: "5px",
                // overflowY: "scroll",
              }}
            >
              <Div>В конце каждого месяца подводятся итоги сезона: первым 10 игрокам в топе по рейтингу даются призы:</Div>
              {/* <Div>сезона: первым</Div>
              <Div>10 игрокам в топе по рейтингу даются</Div>
              <Div>призы:</Div> */}

              <Div>Размер призов:</Div>
              <Div>1 Место — 500₽</Div>
              <Div>2 Место — 300₽</Div>
              <Div>3 Место — 200₽</Div>
              <Div>4 Место — GOLD VIP на 7 дней</Div>
              <Div>5 Место — VIP на 7 дней</Div>
              <Div>6-10 Место — 1000 DK</Div>

              <Div style={{marginTop: "5px"}} >(Каждое место соответствует месту в топе по рейтингу)</Div>
            {/* <Div style={{marginTop: "5px"}} >До конца сезона: {stringTime}</Div> */}
            </Div>
          }
          actions={[
            {
              title: "Понятно",
              mode: "commerce",
              action: () => {
                setActiveModal(null);
              },
            },
          ]}
        ></ModalCard>
      </ModalRoot>
    </>
  );
};

export { ModalSeason };
