//constants
import { QIWI_PUBLIC_KEY, USER_ID } from "../constants";

export function qiwiTypeManager(
  type,
  amount,
  course,
  period,
  vipNameColor,
  setActiveModal,
  openAlertPay
) {
  switch (type) {
    case "durkacoins":
      qiwiPay(amount, course);
      break;
    case "vip":
      qiwiPayVIP(period, vipNameColor);
      break;
    case "gold":
      qiwiPayGoldVIP(period, vipNameColor);
      break;
    case "exchange":
      exchangeQiwiPay(amount, setActiveModal, openAlertPay);
      break;
    default:
      break;
  }
}

function generalQiwiPay(amount, comment) {
  window.QiwiCheckout.createInvoice({
    publicKey: QIWI_PUBLIC_KEY,
    amount,
    comment,
  })
    .then((response) => {
      console.log("general qiwi", response);
    })
    .catch((error) => {
      console.log("GENERAL QIWI ERROR:", error);
    });
}

function qiwiPay(amount, course) {
  generalQiwiPay(amount * course, `durkacoins ${USER_ID}`);
}

function qiwiPayVIP(period, vipNameColor) {
  generalQiwiPay(
    { 1: 29, 7: 139, 30: 499 }[period],
    `vip ${USER_ID} ${vipNameColor} ${period}`
  );
}

function qiwiPayGoldVIP(period, vipNameColor) {
  generalQiwiPay(
    { 1: 199, 7: 1399, 30: 4999 }[period],
    `gold ${USER_ID} ${vipNameColor} ${period}`
  );
}

function exchangeQiwiPay(amount, setActiveModal, openAlertPay) {
  if (!amount || Number.isNaN(+amount) || amount < 1) {
    setActiveModal(null);
    return openAlertPay("Сумма должна быть целым положительным числом", "red");
  }

  generalQiwiPay(amount, `exchange ${USER_ID}`);
}
