import React from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  InfoRow,
  FormLayoutGroup,
  SimpleCell,
  Cell,
  Button,
  Div,
  Link,
  Avatar,
  HorizontalScroll,
} from "@vkontakte/vkui";
import Fade from "react-reveal/Fade";

import { useState, useEffect } from "react";

const ModalIntuitionResult = ({
  id,
  name,
  photoUrl,
  userUrl,
  bank,
  setActiveModal,
  openBase,
  numberResult,
  renderItemResult,
}) => {
  useEffect(() => {
    console.log('BANK', bank);
  }, []);

  return (
    <>
      <ModalRoot activeModal="modal-roulette">
        <ModalCard
          id="modal-roulette"
          onClose={() => setActiveModal(null)}
          // icon={
          //   <Avatar
          //     size={72}
          //     src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
          //   />
          // }
          header="Результаты"
          caption={
            <>
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexDirection: "column",
                  justifyContent: "center",
                  marginTop: 5,
                  marginBottom: 5,
                }}
              >
                <Fade duration={3000}>
                  <Div style={{ marginBottom: "5px" }}>
                    Выпало число {numberResult}
                  </Div>
                </Fade>

                <Div style={{ marginBottom: "5px" }}>
                {renderItemResult.length > 1 ? (
                    'Победителями становятся...'
                  ) : (
                    'Победителем становится...'
                  )}
                </Div>
                <Div
                  style={{
                    fontSize: "15px",
                    fontWeight: "bold",
                    marginBottom: "5px",
                  }}
                >
                  {name}
                </Div>
                <Div style={{ marginBottom: "5px" }}>
                  Поздравляем!
                  {' ' + renderItemResult.length > 1 ? (
                    'И эти игроки забирают по'
                  ) : (
                    'И этот игрок забирает'
                  )}
                </Div>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    flexDirection: "row",
                    userSelect: "none",
                    marginBottom: "10px",
                  }}
                >
                  <Div
                    style={{
                      userSelect: "none",
                      fontWeight: "bold",
                      fontSize: "15px",
                    }}
                  >
                    {bank / renderItemResult.length}
                  </Div>
                  <Div className="iconDC" style={{ marginLeft: 5 }} />
                </Div>
                <HorizontalScroll>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      flexDirection: "row",
                      userSelect: "none",
                    }}
                  >
                    {renderItemResult}
                  </Div>
                </HorizontalScroll>
              </Div>
            </>
          }
          actions={[
            {
              title: "Поделиться с друзьями",
              mode: "commerce",
              action: () => {
                openBase();
              },
            },
          ]}
        ></ModalCard>
      </ModalRoot>
    </>
  );
};

export { ModalIntuitionResult };
