import React from "react";
import {
  ModalRoot,
  ModalCard,
  InfoRow,
  FormLayout,
  Input,
  Div,
  Avatar,
} from "@vkontakte/vkui";

import { formatCoins } from "../helpers";

const ModalSharing = ({ setActiveModal, openBase }) => {
  return (
    <ModalRoot activeModal="modal-sharing">
      <ModalCard
        id="modal-sharing"
        onClose={() => setActiveModal(null)}
        icon={
          <Avatar
            size={72}
            src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
          />
        }
        header="Продай быстрее!"
        caption={
          <>
            <Div
              style={{
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
              }}
            >
              <Div>
                Ваше предложение успешно добавлено! Чтобы ваше предложение
                купили быстрее, Вы можете поделиться ссылкой с друзьями.
              </Div>
            </Div>
          </>
        }
        actions={[
          {
            title: "Рассказать друзьям",
            mode: "commerce",
            action: () => {
              openBase();
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalSharing };
