import React from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";

const ExchangeSell = ({
  go,
  id,
  durkaCoins,
  course,
  addExchangeOffer,
  onChangeAction,
  costOneDC,
  inputDurkaCoins,
  qiwiAccount,
  openInfoVIP,
}) => {
  return (
    <Div
      style={{
        margin: 10,
      }}
    >
      <Group>
        <Cell>
          <InfoRow header="Ваш баланс" className="balanceDCMiddle">
            <Div className="divFlexStart">
              <Div className="balanceDCMiddle">{durkaCoins}</Div>
              <Div className="iconDC" style={{ marginLeft: 4 }} />
            </Div>
          </InfoRow>

          <InfoRow className="balanceDCMiddle" header="Текущий курс">
            <Div className="divFlexStart">
              <Div className="iconDC" style={{ marginRight: 4 }} />
              <Div className="balanceDCMiddle"> = {course}/₽ </Div>
            </Div>
          </InfoRow>

          {/* <InfoRow  header="Итоговая прибыль">
            <Div id="profit" style={{ color: "green" }}> 0 руб </Div>
          </InfoRow> */}

          <Div
            style={{
              marginBottom: 5,
              marginTop: 5,
            }}
          >
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите кол-во DurkaCoins
            </Div>
            <Input
              value={inputDurkaCoins}
              onChange={(e) => onChangeAction(e, "DC")}
              type="text"
            ></Input>

            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите цену за 1 DurkaCoin в руб
            </Div>
            <Input
              value={costOneDC}
              onChange={(e) => onChangeAction(e, "costOneDC")}
              type="text"
            ></Input>
          </Div>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 10,
            }}
          >
            <Group>
              <Button
                style={{ backgroundColor: "green", color: "white" }}
                onClick={addExchangeOffer}
                mode="commerce"
                size="xl"
                stretched
              >
                Добавить предложение
              </Button>
            </Group>

            <Div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                flexDirection: "row",
                marginTop: "10px",
              }}
            >
              Купить VIP аккаунт
            </Div>
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                flexDirection: "row",
                marginTop: "10px",
              }}
            >
              <Button
                style={{
                  backgroundColor: "#FFB800",
                  color: "white",
                  marginRight: "5px",
                }}
                onClick={() => openInfoVIP("goldVIP")}
                mode="commerce"
                size="xl"
                stretched
              >
                GOLD VIP
              </Button>
              <Button
                style={{
                  backgroundColor: "#800080",
                  color: "white",
                  marginLeft: "5px",
                }}
                onClick={() => openInfoVIP("VIP")}
                mode="commerce"
                size="xl"
                stretched
              >
                VIP
              </Button>
            </Div>

            <Group>
              <Div
                style={{
                  marginTop: 10,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  flexDirection: "column",
                }}
              >
                Предложение размещается на 24 часа
              </Div>
              <Div
                style={{
                  marginTop: 10,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  flexDirection: "column",
                }}
              >
                Чем ниже стоимость за 1 DurkaCoin, тем выше предложение в списке
              </Div>
            </Group>
          </Div>
        </Cell>
      </Group>
    </Div>
  );
};

export { ExchangeSell };
