import React from 'react';
import { ModalRoot, ModalCard, InfoRow, FormLayout, Input,} from '@vkontakte/vkui';

const ModalShopBuyItem = ({icon, name, rating, onChangeHandlerAmount, 
  time, buyItem, targetIdShop, setActiveModal }) => {


    return (
      <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={icon}
        header={name}
        caption={
          <>
            {targetIdShop >= 1 && targetIdShop <= 3 ? (
              <>
                <InfoRow>
                  {name} - при использовании прибавляет +{rating} к
                  рейтингу.
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${name}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : name === "Отпуск" ? (
              <>
                <InfoRow>
                  {name} - предоставляет вам отпуск на {time}, в
                  течение этого времни, никто не может использовать на вас
                  'смирительную рубашку' или 'одиночную камеру'
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${name}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : name === "Смирительная рубашка" ? (
              <>
                <InfoRow>
                  {name} - отнимает {rating} рейтинга у любого игрока
                  ДУРКИ
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${name}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : name === "Одиночная камера" ? (
              <>
                <InfoRow>
                  {name} - отнимает {rating} рейтинга у любого игрока
                  ДУРКИ.
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`Укажите кол-во ${name}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : (
              ""
            )}
          </>
        }
        actions={[
          {
            title: "Купить",
            mode: "commerce",
            action: () => {
              buyItem();
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
    );
};

export { ModalShopBuyItem };

