import bridge from "@vkontakte/vk-bridge";

//constants
import { APP_ID, GROUP_ID, USER_ID } from "../constants";

export function vkPayTypeManager(
  type,
  amount,
  course,
  period,
  vipNameColor,
  setActiveModal,
  openAlertPay
) {
  switch (type) {
    case "durkacoins":
      payVkPay(amount, course);
      break;
    case "vip":
      payVkPayVIP(period, vipNameColor);
      break;
    case "gold":
      payVkPayGoldVIP(period, vipNameColor);
      break;
    case "exchange":
      payVkPayExchange(amount, setActiveModal, openAlertPay);
      break;
    default:
      break;
  }
}

function generalVkPay(amount, comment = "") {
  bridge
    .sendPromise("VKWebAppOpenPayForm", {
      app_id: APP_ID,
      action: "pay-to-group",
      params: {
        amount: amount,
        group_id: GROUP_ID,
        description: comment,
      },
    })
    .catch((error) => {
      console.log(error);
    });
}

function payVkPay(amount, course) {
  generalVkPay(Number((amount * course).toFixed(2)));
}

function payVkPayVIP(period, vipNameColor) {
  generalVkPay(
    { 1: 29, 7: 139, 30: 499 }[period],
    `vip ${vipNameColor} ${period} (не менять)`
  );
}

function payVkPayGoldVIP(period, vipNameColor) {
  generalVkPay(
    { 1: 199, 7: 1399, 30: 4999 }[period],
    `gold ${vipNameColor} ${period} (не менять)`
  );
}

function payVkPayExchange(amount, setActiveModal, openAlertPay) {
  if (!amount || Number.isNaN(+amount) || amount < 1) {
    setActiveModal(null);
    return openAlertPay("Сумма должна быть целым положительным числом", "red");
  }
  generalVkPay(amount, `exchange ${USER_ID}`);
}