import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  Button,
  Group,
  Link,
} from "@vkontakte/vkui";

import {
  GameItem,
} from "../gamesItem";

import { dataGames, SocketManager,  } from "../helpers";


const { callMethod } = SocketManager;

const Games = ({
  id,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  go,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,

}) => {
  // const [amountDurkaCoins, setAmountDurkaCoins] = useState("");
  const [activeTab, setActiveTab] = useState("buy");

  const renderGames = dataGames.map((item, index) => {
    return (
      <GameItem
        key={index}
        id={item.id}
        go={go}
        nameGame={item.nameGame}
        icon={item.icon}
        namePanelGame={item.namePanelGame}
        discription={item.discription}
        
      />
    );
  });


  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        Игры
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
        <>
          {networkStatus ? (
            stub(
              "Отсутствует интернет - соединение!",
              updateConnection,
              "Повторить"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "Ошибка подключения к серверу",
                  socketReconnect,
                  "Повторить"
                )
              ) : (
                <>
                  <Div
                    style={{
                      marginLeft: 10,
                    }}
                  >
                      {renderGames}
                  </Div>
               
                
                </>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Games };
