import React from "react";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Link from "@vkontakte/vkui/dist/components/Link/Link";

import { formatCoins } from "../helpers";

const StickersItem = ({
  id,
  name,
  price,
  icon,
  openInfoStickers,
  openBuyItem,
  scheme,
  buyItem,
  openBuyMining
}) => {
  return (
    <Div
      className={scheme === "bright_light" ? "cardShopBlack" : "cardShopGray"}
    >
      <Cell
        className="myItem"
        before={icon}
        asideContent={
          <Link id={id} onClick={openBuyMining} mode="tertiary">
            купить
          </Link>
        }
        description={
          <Div className="div">
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
                justifyContent: "start",
                marginTop: 5,
                marginBottom: 5,
                padding: 0,
              }}
            >
              {`${formatCoins(price)}`}
              <Div className="iconDC" style={{ marginLeft: 8 }} />
            </Div>
            <Div className="div">
              <Link id={id} onClick={openInfoStickers}>
                Подробнее
              </Link>
            </Div>
          </Div>
        }
      >
        {name}
      </Cell>
    </Div>
  );
};

export { StickersItem };
