import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  PullToRefresh,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";
import axios from "axios";
import Icon20QuestionCircleFillGreen from "@vkontakte/icons/dist/20/question_circle_fill_green";
import Icon16InfoOutline from "@vkontakte/icons/dist/16/info_outline";

import { ListUsers, ListGroups } from "../components";
import {
  formatCoins,
  shop,
  lootBox,
  stickers,
  mining,
  SocketManager,
} from "../helpers";

//constants
import {
  APP_ID,
  GROUP_ID,
  QIWI_PUBLIC_KEY,
  NAME_PROJECT,
  NAME_PROJECT_LOWER_CASE,
} from "../constants";

const { callMethod } = SocketManager;

const Top = ({
  ranks,
  id,
  userPlace,
  rating,
  goHome,
  vk,
  networkStatus,
  updateConnection,
  snackbar,
  openAlertPay,
  stub,
  socketReconnection,
  socketReconnect,
  connectTwoDevices,
  openMenuVIP,
  openSeasonInfo,
}) => {
  const [userToken, setUserToken] = useState(null);
  const [activeTab, setActiveTab] = useState("general");
  const [friendsTop, setFriendsTop] = useState(null);
  const [renderFriends, setRenderFriends] = useState([]);
  const [fetching, setFetching] = useState(false);
  const [renderGlobal, setRenderGlobal] = useState(0);
  const [renderGroups, setRenderGroups] = useState(0);
  let groupsTop = [];
  let targetIdGroup = 0;


  function getSeason() {
    const date = new Date();
    let count = 2;
    switch (date.getFullYear()) {
      case 2022:
        count = 13;
        break;
      case 2023:
        count = 25;
        break;
      case 2024:
        count = 37;
        break;
      default:
        count = 2;
        break;
    }
    let season = date.getMonth() + count;
    return season;
  }

  useEffect(() => {
    getUsers();
    setTimeout(() => {
      getGroups();
    }, 2000);
  }, []);

  function addGroupTop(e) {
    targetIdGroup = e.currentTarget.id;
    let urlGroup = groupsTop[targetIdGroup - 1].id;
    console.log("urlGroup", urlGroup);
    bridge
      .sendPromise("VKWebAppJoinGroup", {
        group_id: urlGroup,
      })
      .then((res) => {
        if (res.result) {
          openAlertPay("Вы уже подписаны на эту группу", "green");
        }

        console.log("res", res.result);
      })
      .catch((err) => {
        if (err.error_data.error_reason === "User can not join this group") {
          openAlertPay("Подайте заявку, чтобы вступить в группу", "red");
        }
        console.log("err", err);
      });
  }

  function tabGeneral() {
    setActiveTab("general");
    window.scrollTo(0, 0);
  }

  function tabFriends() {
    setActiveTab("friends");
    getFriendsAccess();
    window.scrollTo(0, 0);
  }

  function tabGroups() {
    setActiveTab("groups");
    window.scrollTo(0, 0);
  }

  function getUsers() {
    callMethod("getUsersTop").then(({ status, message }) => {
      if (status === "success") {
        setRenderGlobal(
          message.map((item, index) => {
            return (
              <ListUsers
                key={index}
                id={index + 1}
                idVk={item.id}
                isVIP={item.isVIP}
                vipNameColor={item.vipNameColor}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                rating={item.rating}
                amount={item.durkaCoins}
                ranks={ranks || []}
                openMenuVIP={openMenuVIP}
                activeTab={activeTab}
                type={"general"}
              />
            );
          })
        );
      }

      setFetching(false);
    });
  }

  function getGroups() {
    callMethod("getGroupsTop").then(({ status, message }) => {
      console.log({ message, status });
      if (status === "success") {
        groupsTop = message;
        console.log("setGroupsTop", groupsTop);
        setRenderGroups(
          message.map((item, index) => {
            return (
              <ListGroups
                key={index}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                name={item.name}
                rating={item.rating}
                addGroupTop={addGroupTop}
              />
            );
          })
        );
      }

      setFetching(false);
    });
  }

  function onRefresh() {
    // ВЫЗВАТЬ ЗАПРОС ДАННЫХ ДЛЯ ОТДЕЛЬНОГО ТОПА , ОТОШЕЛ НАЛИТЬ ЧАЯ
    setFetching(true);
    switch (activeTab) {
      case "general":
        getUsers();
        break;
      case "friends":
        getFriendsAccess();
        break;
      case "groups":
        getGroups();
        break;
      default:
        break;
    }
  }

  function getFriendsAccess() {
    bridge
      .sendPromise("VKWebAppGetAuthToken", {
        scope: "friends",
        app_id: APP_ID,
      })
      .then((res) => {
        if (res.access_token) {
          setUserToken(res.access_token);
          getAndSendToServerFriends(res.access_token);
        } else {
          // TODO: Сделать ошибку о том, что пользователь не предоставил доступ к друзьям
        }
      })
      .catch((err) => {
        vk.log(err);
      });
  }

  function getAndSendToServerFriends(userToken) {
    bridge
      .sendPromise("VKWebAppCallAPIMethod", {
        method: "friends.get",
        params: {
          access_token: userToken,
          v: "5.103",
        },
      })
      .then((res) => {
        if (res.response) {
          callMethod("getFriendsTop", {
            items: res.response.items,
          }).then(({ message }) => {
            if (!message.length) {
              return setFriendsTop("У Вас нет друзей в игре");
            }

            console.log(message);

            setFriendsTop(true);
            setFetching(false);
            setRenderFriends(
              message.map((item, index) => {
                return (
                  <ListUsers
                    key={index}
                    id={index + 1}
                    idVk={item.id}
                    isVIP={item.isVIP}
                    photoURL={item.photoURL}
                    vipNameColor={item.vipNameColor}
                    firstName={item.firstName}
                    lastName={item.lastName}
                    rating={item.rating}
                    amount={item.durkaCoins}
                    ranks={ranks || []}
                    openMenuVIP={openMenuVIP}
                    type={"friends"}
                  />
                );
              })
            );
          });
        } else {
          return setFriendsTop("Ошибка при загрузке друзей");
        }
      })
      .catch((err) => {
        return setFriendsTop("Ошибка при загрузке друзей");
      });
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        Топ
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
          <>
            <FixedLayout vertical="top" filled={true}>
              <Tabs>
                <TabsItem onClick={tabGeneral} selected={activeTab === "general"}>
                  Общий
              </TabsItem>
                <TabsItem onClick={tabFriends} selected={activeTab === "friends"}>
                  Друзья
              </TabsItem>
                <TabsItem onClick={tabGroups} selected={activeTab === "groups"}>
                  Сообщества
              </TabsItem>
              </Tabs>
            </FixedLayout>

            {networkStatus ? (
              stub(
                "Отсутствует интернет - соединение!",
                updateConnection,
                "Повторить"
              )
            ) : (
                <>
                  {socketReconnection ? (
                    stub(
                      "Ошибка подключения к серверу",
                      socketReconnect,
                      "Повторить"
                    )
                  ) : (
                      <>
                        {activeTab === "general" ? (
                          <>
                            {renderGlobal.length ? (
                              <>
                                <Div className="headerTop">
                                  <Div
                                    style={{
                                      display: "flex",
                                      alignItems: "center",
                                      flexDirection: "row",
                                      marginTop: "5px",
                                    }}
                                  >
                                    <Div>Сезон: {getSeason()}</Div>
                                    <Icon16InfoOutline
                                      className="animationIcons"
                                      fill={"orange"}
                                      width={16}
                                      height={16}
                                      style={{ marginLeft: "5px" }}
                                      onClick={openSeasonInfo}
                                    />
                                  </Div>

                                  <Div
                                    style={{
                                      display: "flex",
                                      alignItems: "center",
                                      flexDirection: "row",
                                      marginTop: "5px",
                                    }}
                                  >
                                    <Div>Ваше место: {userPlace}</Div>
                                    <Div
                                      style={{
                                        display: "flex",
                                        alignItems: "center",
                                        flexDirection: "row",
                                      }}
                                    >
                                      <Div style={{ marginLeft: 5 }}>
                                        Рейтинг: {rating}
                                      </Div>
                                      <Div
                                        className="iconStar"
                                        style={{ marginLeft: 5 }}
                                      />
                                    </Div>
                                  </Div>
                                </Div>
                                <PullToRefresh
                                  onRefresh={onRefresh}
                                  isFetching={fetching}
                                  style={{ marginTop: 10 }}
                                >
                                  <Div>{renderGlobal}</Div>
                                </PullToRefresh>
                              </>
                            ) : (
                                <div
                                  style={{
                                    display: "flex",
                                    alignItems: "center",
                                    flexDirection: "column",
                                  }}
                                >
                                  <Spinner size="large" style={{ marginTop: 90 }} />
                                </div>
                              )}
                          </>
                        ) : (
                            ""
                          )}

                        {activeTab === "friends" ? (
                          <div style={{ marginTop: 40 }}>
                            {friendsTop === null ? (
                              <div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "column",
                                }}
                              >
                                <Spinner size="large" style={{ marginTop: 90 }} />
                              </div>
                            ) : !renderFriends.length ? (
                              <Div>{friendsTop}</Div>
                            ) : (
                                  <PullToRefresh
                                    onRefresh={onRefresh}
                                    isFetching={fetching}
                                    style={{ marginTop: 10 }}
                                  >
                                    <Div>{renderFriends}</Div>
                                  </PullToRefresh>
                                )}
                          </div>
                        ) : (
                            ""
                          )}

                        {activeTab === "groups" ? (
                          <div style={{ marginTop: 40 }}>
                            {renderGroups.length ? (
                              <PullToRefresh
                                onRefresh={onRefresh}
                                isFetching={fetching}
                                style={{ marginTop: 10 }}
                              >
                                <Div>{renderGroups}</Div>
                              </PullToRefresh>
                            ) : (
                                <div
                                  style={{
                                    display: "flex",
                                    alignItems: "center",
                                    flexDirection: "column",
                                  }}
                                >
                                  <Spinner size="large" style={{ marginTop: 90 }} />
                                </div>
                              )}
                          </div>
                        ) : (
                            ""
                          )}
                      </>
                    )}

                  {snackbar}
                </>
              )}
          </>
        )}
    </Panel>
  );
};

export { Top };
