import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Div,
} from "@vkontakte/vkui";
import axios from "axios";

import { OperationsHistoryItem } from "../components";
import { SocketManager, formatDate } from "../helpers";

const { callMethod } = SocketManager;

const OperationsHistory = ({
  id,
  goHome,
  fetchedUser,
}) => {
  const [dataOperationsHistory, setDataOperationsHistory] = useState([]);

  useEffect(() => {
    getOperationsHistory();
  }, []);

  function getOperationsHistory() {
    callMethod('getTransfersHistory')
      .then(({ status, message }) => {
        setDataOperationsHistory(message);
        console.log("getOperationsHistory", dataOperationsHistory);
      });
  }

  // function formatDate(date) {
  //   date = new Date(date * 1000);

  //   let dd = date.getDate();
  //   if (dd < 10) dd = "0" + dd;
  //   let mm = date.getMonth() + 1;
  //   if (mm < 10) mm = "0" + mm;
  //   let yy = date.getFullYear() % 100;
  //   if (yy < 10) yy = "0" + yy;
  //   let hh = date.getHours();
  //   if (hh < 10) hh = "0" + hh;
  //   let minutes = date.getMinutes();
  //   if (minutes < 10) minutes = "0" + minutes;

  //   return dd + "." + mm + "." + yy + " " + hh + ":" + minutes;
  // }

  const renderOperationsHistoryItem = dataOperationsHistory.map(
    (item, index) => {
      return (
        <OperationsHistoryItem
          key={index}
          senderId={item.senderId}
          targetId={item.targetId}
          amount={item.amount}
          createdAt={item.createdAt}
          senderName={item.senderName}
          targetName={item.targetName}
          senderPhoto={item.senderPhoto}
          targetPhoto={item.targetPhoto}
          formatDate={formatDate}
          fetchedUser={fetchedUser}
        />
      );
    }
  );

  return (
    <Panel id={id} separator={false} >
      <PanelHeaderSimple left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
        История операций
      </PanelHeaderSimple>

      {!dataOperationsHistory ? (
        <Div style={{ marginLeft: 30 }}>У вас еще нету ни одной операции.</Div>
      ) : (
        <Div>{renderOperationsHistoryItem}</Div>
      )}
    </Panel>
  );
};

export { OperationsHistory };


