import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  Button,
  Group,
  Link,
} from "@vkontakte/vkui";

import {
  InventoryItem,
  MiningInventoryItem,
  ListUsers,
  ExchangeSell,
  ExchangeBuyItem,
} from "../components";

import {
  ModalExchangeBuy,
} from "../modal";

import { formatCoins, SocketManager, formatCoinsToServer } from "../helpers";
const { callMethod } = SocketManager;

const Exchange = ({
  id,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  dataMiners,
  miners,
  durkaCoins,
  course,
  openAlertPay,
  setActiveModal,
  openBuyDurkaCoins,
  rubles,
  openReplenishment,
  openWithdraw,
  setActivePanel,
  fetchedUser,
  openSharingModal,
  openInfoVIP,
  isVIP,
  vipType
}) => {
  let amountDurkaCoins = 0;
  const [costOneDC, setCostOneDC] = useState("");
  const [qiwiAccount, setQiwiAccount] = useState("+7");
  const [inputDurkaCoins, setInputDurkaCoins] = useState("");
  const [sliceItem, setSliceItem] = useState(50);

  // const [amountDurkaCoins, setAmountDurkaCoins] = useState("");

  const [activeTab, setActiveTab] = useState("buy");
  const [dataExchangeBuy, setDataExchangeBuy] = useState([]);
  let targetIdExchangeBuy = 0;

  function tabBuy() {
    setActiveTab("buy");
    window.scrollTo(0, 0);
  }

  function tabSell() {
    setActiveTab("sell");
    window.scrollTo(0, 0);
  }

  function getExchangeList() {
    callMethod("getExchangeList").then(({ message }) => {
      setDataExchangeBuy(message);
      // setDataExchangeBuy([]);
    });
  }

  function deleteExchangeOffer(userId) {
    console.log("value", userId);

    callMethod("deleteExchangeOffer", {
      ownerId: userId, // передать айди юзера чье предложение удаляем
    }).then(({ status, message }) => {
      if (status === 'req_limit') return;
      openAlertPay(message, status === "denied" ? "red" : "green");

      if (status === "success") {
        getExchangeList();
      }
    });
  }

  useEffect(() => {
    getExchangeList();
  }, []);

  function addExchangeOffer() {
    if (!inputDurkaCoins || !costOneDC || !qiwiAccount) {
      return openAlertPay("Ты забыл заполнить все поля", "red");
    }

    console.log({
      inputDurkaCoins,
      costOneDC,
      qiwiAccount,
    });

    if (!qiwiAccount.startsWith("+7")) {
      // Модалку о том что должен начинаться на +7
      return openAlertPay("Введи нормальный номер шизоид", "red");
    }

    if (
      Number.isNaN(+inputDurkaCoins) ||
      (+inputDurkaCoins ^ 0) !== +inputDurkaCoins
    ) {
      return openAlertPay(
        "Кол-во DurkaCoins должно быть целым числом, тебя кто из палаты выпустил?",
        "red"
      );
    }

    if (costOneDC.length > 4) {
      return openAlertPay("В числе должны быть 2 знака после запятой", "red");
    }

    if (costOneDC >= course) {
      return openAlertPay(
        "Стоимость одного DurkaCoin должна быть ниже текущего курса",
        "red"
      );
    }

    let oneLvl = +(course * 0.5).toFixed(2); // кол-во коинов < 51
    let twoLvl = +(course * 0.4).toFixed(2); // кол-во коинов > 51
    let threeLvl = +(course * 0.3).toFixed(2); // кол-во коинов > 251
    let vipLvl = +(course * 0.2).toFixed(2); // если вип
    let goldLvl = +(course * 0.1).toFixed(2); // если голд

    // ([236908027, 457421325, 597298271, 457203217, 432350015, 597232247].includes(fetchedUser.id))

    // console.log("isVIP", isVIP);
    // console.log("inputDurkaCoins", +inputDurkaCoins);
    // console.log("+costOneDC", +costOneDC);
    // console.log("oneLvl", oneLvl);
    // console.log("twoLvl", twoLvl);
    // console.log("threeLvl", threeLvl);
    // console.log("vipLvl", vipLvl);
    // console.log("vipType", vipType);

    // условия для вип аккаунтов
    let _costOneDc = Number(costOneDC);
    let _inputDurkaCoins = Number(inputDurkaCoins);

    if (isVIP && vipType === 2 && _costOneDc < goldLvl) {
      return openAlertPay(
        `Для вашего кол-ва DurkaCoins, стоимость 1 DurkaCoins должна быть выше ${goldLvl} руб`,
        "red"
      );
    } else if (isVIP && vipType === 1 && _costOneDc < vipLvl) {
      return openAlertPay(
        `Для вашего кол-ва DurkaCoins, стоимость 1 DurkaCoins должна быть выше ${vipLvl} руб,
        купите VIP Аккаунт чтобы продавать дешевле`,
        "red"
      );
    } else if (!isVIP && _inputDurkaCoins <= 50 && _costOneDc < oneLvl) {
      return openAlertPay(
        `Для вашего кол-ва DurkaCoins, стоимость 1 DurkaCoins должна быть выше ${oneLvl} руб,
        купите VIP Аккаунт чтобы продавать дешевле`,
        "red"
      );
    } else if (!isVIP && _inputDurkaCoins >= 51 && _inputDurkaCoins <= 250 && _costOneDc < twoLvl) {
      return openAlertPay(
        `Для вашего кол-ва DurkaCoins, стоимость 1 DurkaCoins должна быть выше ${twoLvl} руб,
        купите VIP Аккаунт чтобы продавать дешевле`,
        "red"
      );
    } else if (!isVIP && _inputDurkaCoins >= 251 && _costOneDc < threeLvl) {
      return openAlertPay(
        `Для вашего кол-ва DurkaCoins, стоимость 1 DurkaCoins должна быть выше ${threeLvl} руб,
        купите VIP Аккаунт чтобы продавать дешевле`,
        "red"
      );
    }

    if (Number.isNaN(+costOneDC)) {
      return openAlertPay("Стоимость должна быть числом, алло...", "red");
    }

    if (inputDurkaCoins * 100_000 > durkaCoins) {
      return openAlertPay("У тебя не хватает деняк на балансе лУл", "red");
    }

    callMethod("createExchangeOffer", {
      qiwiAccount,
      costOneDC,
      durkaCoins: inputDurkaCoins,
    })
      .then(({ status, message }) => {
        console.log({ status, message });
        if (status === 'req_limit') return;

        if (status === "denied") {
          return openAlertPay(message, "red");
        }

        // openAlertPay(message, "green"); // сообщение об успешном добавлении предложзения
        getExchangeList();

        setCostOneDC("");
        setInputDurkaCoins("");
        setQiwiAccount("+7");
        tabBuy();
        openSharingModal();
      })
      .catch((error) => {
        console.log("createExchangeOffer error", error);
      });
  }

  function onChangeAction(e, type, _costOneDC, remainingDC) {
    const value = e.target.value.trim();

    switch (type) {
      case "DC":
        setInputDurkaCoins(value);
        break;
      case "costOneDC":
        setCostOneDC(value.replace(",", "."));
        break;
      case "qiwiAccount":
        setQiwiAccount(value);
        break;
      case "modal":
        let text = `К оплате: ${(value * _costOneDC).toFixed(2)} руб`;
        if (Number.isNaN(+value)) {
          text = `Сумма должна быть числом`;
        }

        if ((+value ^ 0) !== +value) {
          text = "DurkaCoins должны быть целым числом";
        }

        if (remainingDC / 100_000 < value) {
          text = `Кол-во не должно превышать оставшиеся на продажу DurkaCoins`;
        }

        document.querySelector("#exchangeBuy").innerHTML = text;
        amountDurkaCoins = Number(value);
        break;
    }
  }

  function buyExchangeOffer(userId) {
    callMethod("buyExchangeOffer", {
      durkaCoins: amountDurkaCoins,
      offerUserId: userId,
    }).then(({ status, message }) => {
      getExchangeList();
      setActiveModal(null);
      return openAlertPay(message, status === "denied" ? "red" : "green");
    });
  }

  function openExchangeBuy(e) {
    targetIdExchangeBuy = e.currentTarget.id;
    const { costOneDC, remainingDC, userId } = dataExchangeBuy[
      targetIdExchangeBuy - 1
    ];

    setActiveModal(
      <ModalExchangeBuy
        userId={userId}
        targetIdExchangeBuy={targetIdExchangeBuy}
        setActiveModal={setActiveModal}
        costOneDC={costOneDC}
        remainingDC={remainingDC}
        onChangeAction={onChangeAction}
        buyExchangeOffer={buyExchangeOffer}
      />
    );
  }

  // let sliceItem = 50;
  const renderExchangeBuy = dataExchangeBuy
    .slice(0, sliceItem)
    .map((item, index) => {
      const dc = item.durkaCoins / 100_000;
      const remDC = item.remainingDC / 100_000;
      const urlUser = "https://vk.com/id" + item.userId;
      const sumRUB = ((item.durkaCoins / 100_000) * item.costOneDC).toFixed(2);
      const remainingAmountRUB = (
        (item.remainingDC / 100_000) *
        item.costOneDC
      ).toFixed(2);

      return (
        <ExchangeBuyItem
          key={index}
          id={index + 1}
          userId={item.userId}
          fullName={item.fullName}
          photoURL={item.photoURL}
          costOneDC={item.costOneDC}
          openExchangeBuy={openExchangeBuy}
          urlUser={urlUser}
          sumRUB={sumRUB}
          remainingAmountRUB={remainingAmountRUB}
          dc={dc}
          remDC={remDC}
          fetchedUser={fetchedUser}
          deleteExchangeOffer={deleteExchangeOffer}
        />
      );
    });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "row",
            }}
          >
            <PanelHeaderBack onClick={goHome} data-to="home" />
            <Div
              style={{
                fontSize: "10px",
                color: "green",
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
              }}
            >
              <Div>Сообщить об</Div>
              <Div>ошибке:</Div>
              <Div>
                <Link href="https://vk.me/fakeman.cat_fmc" target="_blank">
                  Поддержка
                </Link>
              </Div>
            </Div>
          </Div>
        }
      >
        Биржа (beta)
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem onClick={tabBuy} selected={activeTab === "buy"}>
                Купить
              </TabsItem>
              <TabsItem onClick={tabSell} selected={activeTab === "sell"}>
                Продать
              </TabsItem>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "Отсутствует интернет - соединение!",
              updateConnection,
              "Повторить"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "Ошибка подключения к серверу",
                  socketReconnect,
                  "Повторить"
                )
              ) : (
                <>
                  {activeTab === "buy" ? (
                    <>
                      {/* {renderInventory.length
                                                ? */}

                      <Div style={{ marginTop: 55 }}>
                        <Group>
                          <Div
                            style={{
                              display: "flex",
                              flexDirection: "column",
                              marginLeft: 20,
                              marginRight: 20,
                            }}
                          >
                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                flexDirection: "row",
                                justifyContent: "space-between",
                                // marginLeft: 20,
                                // marginRight: 20,
                                marginBottom: 10,
                              }}
                            >
                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "row",
                                }}
                              >
                                <Div>Баланс: {formatCoins(durkaCoins)}</Div>
                                <Div
                                  className="iconDC"
                                  style={{ marginLeft: 8 }}
                                />
                              </Div>

                              <Div>
                                <Button
                                  style={{
                                    backgroundColor: "green",
                                    color: "white",
                                  }}
                                  size="m"
                                  onClick={openBuyDurkaCoins}
                                >
                                  Пополнить
                                </Button>
                              </Div>
                            </Div>
                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                flexDirection: "row",
                                justifyContent: "space-between",
                                // marginLeft: 20,
                                // marginRight: 20
                              }}
                            >
                              <Div>Баланс: {rubles} ₽</Div>
                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "row",
                                }}
                              >
                                <Button
                                  style={{
                                    backgroundColor: "#D40000",
                                    color: "white",
                                  }}
                                  size="m"
                                  onClick={openWithdraw}
                                  // style={{ marginLeft: 8 }}
                                >
                                  Вывести
                                </Button>
                                <Button
                                  size="m"
                                  onClick={openReplenishment}
                                  style={{
                                    marginLeft: 8,
                                    backgroundColor: "green",
                                    color: "white",
                                  }}
                                >
                                  Пополнить
                                </Button>
                              </Div>
                            </Div>
                          </Div>
                        </Group>

                        <Group>
                          <Div
                            style={{
                              paddingLeft: 5,
                              paddingRight: 5,
                            }}
                          >
                            <Div
                              style={{
                                paddingLeft: 5,
                                paddingRight: 5,
                              }}
                            >
                              <Button
                                size="l"
                                stretched
                                onClick={getExchangeList}
                                style={{
                                  marginTop: 10,
                                  marginBottom: 10,
                                  backgroundColor: "green",
                                  color: "white",
                                }}
                              >
                                Обновить список
                              </Button>
                            </Div>
                            <>
                              {!renderExchangeBuy.length ? (
                                <div className="spinner">
                                  <Spinner
                                    size="large"
                                    style={{ marginTop: 90 }}
                                  />
                                </div>
                              ) : (
                                <>
                                  <Div> {renderExchangeBuy} </Div>
                                  <Div
                                    style={{
                                      marginTop: "15px",
                                      marginBottom: "15px",
                                    }}
                                  >
                                    <Button
                                      size="l"
                                      stretched
                                      onClick={() =>
                                        setSliceItem(sliceItem + 50)
                                      }
                                      style={{
                                        // marginTop: 10,
                                        // marginBottom: 10,
                                        backgroundColor: "green",
                                        color: "white",
                                      }}
                                    >
                                      Показать ещё
                                    </Button>
                                  </Div>
                                </>
                              )}
                            </>
                          </Div>
                        </Group>
                      </Div>
                      {/* // : */}
                      {/* <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 90 }} />
                      </div> */}
                      {/* // } */}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "sell" ? (
                    <>
                      <Div
                        style={{
                          marginTop: 55,
                        }}
                      >
                        <ExchangeSell
                          durkaCoins={formatCoins(durkaCoins)}
                          course={course}
                          addExchangeOffer={addExchangeOffer}
                          onChangeAction={onChangeAction}
                          costOneDC={costOneDC}
                          inputDurkaCoins={inputDurkaCoins}
                          qiwiAccount={qiwiAccount}
                          openInfoVIP={openInfoVIP}
                        />
                      </Div>
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Exchange };
