import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  Div,
  FormLayoutGroup,
} from "@vkontakte/vkui";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

const ModalCalendar = ({ setActiveModal, getChart, openAlertPay }) => {
  const [date, setDate] = useState(new Date());

  return (
    <>
      <ModalRoot activeModal="modal-calendar">
        <ModalCard
          id="modal-calendar"
          onClose={() => setActiveModal(null)}
          icon={
            <Avatar
              size={72}
              src="https://sun9-50.userapi.com/c857036/v857036390/aa9a0/i-iQZ4973FQ.jpg"
            />
          }
          header="Выбрать период"
          caption={
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                justifyContent: "center",
              }}
            >
              <Calendar
                selectRange={true}
                onChange={(date) => {
                  setDate(date);
                  console.log({
                    startDate: date[0].getTime(),
                    endDate: date[1].getTime(),
                  });
                }}
                returnValue="range"
              />
            </Div>
          }
          actions={[
            {
              title: "Выбрать",
              mode: "commerce",
              action: () => {
                setActiveModal(null);
                if (Date.now() < date[0].getTime()) {
                  return openAlertPay("Укажите период раньше текущей даты");
                }
                getChart(date[0].getTime(), date[1].getTime());
              },
            },
          ]}
        ></ModalCard>
      </ModalRoot>
    </>
  );
};

export { ModalCalendar };
