import React from 'react';
import { ModalRoot, ModalCard, FormLayout, Input,} from '@vkontakte/vkui';

const ModalInventoryUseItem = ({targetIdInventory, icon, name, setActiveModal, onChangeHandlerId, $useItem}) => {


    return (
      <ModalRoot activeModal="use-item">
          <ModalCard
            id="use-item"
            onClose={() => setActiveModal(null)}
            icon={icon}
            header={name}
            caption={
              <>
                <FormLayout>
                  <Input
                    top="Укажите ссылку на пользователя"
                    placeholder="https://vk.com/durkacoin"
                    onChange={onChangeHandlerId}
                    type="text"
                  />
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Использовать",
                mode: "commerce",
                action: () => {
                  $useItem(targetIdInventory);
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
    );
};

export { ModalInventoryUseItem };

