import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
  Div,
  Radio,
} from "@vkontakte/vkui";

const ModalInfoVIP = ({
  setActiveModal,
  openPaymentVIP,
  type,
  openPaymentGoldVIP,
}) => {
  const [vipNameColor, setVipNameColor] = useState(2);
  const [period, setPeriod] = useState(1);

  const targetId = (e) => {
    setVipNameColor(+e.currentTarget.id);
    console.log("targetId vipNameColor", vipNameColor);
    console.log("targetId ", +e.currentTarget.id);
  };

  useEffect(() => {
    console.log("period", period);
  }, [period]);

  return (
    <>
      {type === "VIP" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            header="Что дает VIP аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Продажа DurkaCoins ниже рыночной стоимости на 80%{" "}
                  </InfoRow>
                  <InfoRow>2. Цветной никнейм в ТОП-е, играх</InfoRow>
                  <InfoRow>
                    3. Gold пилюля -{period === 1 ? 1 : period === 7 ? 3 : 10}
                    шт{" "}
                  </InfoRow>
                  <InfoRow>
                    4. Одиночная камера -
                    {period === 1 ? 10 : period === 7 ? 30 : 100}
                    шт{" "}
                  </InfoRow>
                  <InfoRow>5. Эксклюзивные стикеры НАРУТО</InfoRow>
                  <InfoRow>6. Стикеры Время приключений</InfoRow>
                  <InfoRow>
                    7. Ограничение по времени на переводы снижена до 10 сек.
                  </InfoRow>
                  <InfoRow>Выберите период: </InfoRow>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <Radio
                      name="radio"
                      value="1"
                      defaultChecked
                      onClick={() => setPeriod(1)}
                    >
                      1 день
                    </Radio>
                    <Radio
                      name="radio"
                      value="7"
                      // disabled
                      onClick={() => setPeriod(7)}
                    >
                      7 дней
                    </Radio>
                    <Radio
                      name="radio"
                      value="30"
                      // defaultChecked
                      onClick={() => setPeriod(30)}
                    >
                      30 дней
                    </Radio>
                  </Div>
                  <InfoRow style={{ marginBottom: "10px" }}>
                    Выберите цвет:{" "}
                  </InfoRow>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <Div
                      id="1"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "green",
                        border: vipNameColor === 1 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="2"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#D40000",
                        border: vipNameColor === 2 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="3"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "blue",
                        border: vipNameColor === 3 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="4"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#800080",
                        border: vipNameColor === 4 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                  </Div>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате:{" "}
                    {period === 1
                      ? "29 руб"
                      : period === 7
                      ? "139 руб"
                      : "499 руб"}
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Купить VIP",
                mode: "commerce",
                action: () => {
                  openPaymentVIP(vipNameColor, period);
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        <ModalRoot activeModal="modal-info-gold-vip">
          <ModalCard
            id="modal-info-gold-vip"
            onClose={() => setActiveModal(null)}
            header="Что дает GOLD VIP аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Продажа DurkaCoins ниже рыночной стоимости на 90%{" "}
                  </InfoRow>
                  <InfoRow>2. Добавление в контакты группы</InfoRow>
                  <InfoRow>3. Цветной никнейм в ТОП-е, играх</InfoRow>
                  <InfoRow>
                    4. Gold пилюля -
                    {period === 1 ? 10 : period === 7 ? 30 : 100}
                    шт{" "}
                  </InfoRow>
                  <InfoRow>
                    5. Одиночная камера -
                    {period === 1 ? 100 : period === 7 ? 300 : 1000}
                    шт{" "}
                  </InfoRow>
                  <InfoRow>
                    6. Отпуск -{period === 1 ? 100 : period === 7 ? 300 : 1000}
                    шт{" "}
                  </InfoRow>
                  <InfoRow>7. Все стикеры из магазина ДУРКИ</InfoRow>
                  <InfoRow>
                    8. Ограничение по времени на переводы снижена до 10 сек.
                  </InfoRow>
                  <InfoRow>Выберите период: </InfoRow>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <Radio
                      name="radio"
                      value="1"
                      defaultChecked
                      onClick={() => setPeriod(1)}
                    >
                      1 день
                    </Radio>
                    <Radio
                      name="radio"
                      value="7"
                      // disabled
                      onClick={() => setPeriod(7)}
                    >
                      7 дней
                    </Radio>
                    <Radio
                      name="radio"
                      value="30"
                      // defaultChecked
                      onClick={() => setPeriod(30)}
                    >
                      30 дней
                    </Radio>
                  </Div>
                  <InfoRow style={{ marginBottom: "10px" }}>
                    Выберите цвет:{" "}
                  </InfoRow>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <Div
                      id="1"
                   
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "green",
                        border: vipNameColor === 1 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="2"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#D40000",
                        border: vipNameColor === 2 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="3"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "blue",
                        border: vipNameColor === 3 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="4"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#800080",
                        border: vipNameColor === 4 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                  </Div>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате:{" "}
                    {period === 1
                      ? "199 руб"
                      : period === 7
                      ? "1399 руб"
                      : "4999 руб"}
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Купить GOLD VIP",
                mode: "commerce",
                action: () => {
                  openPaymentGoldVIP(vipNameColor, period);
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      )}
    </>
  );
};

export { ModalInfoVIP };
