import React from 'react';
import { Panel, PanelHeaderSimple, PanelHeaderBack, Group, Header, Cell, Div, Link } from '@vkontakte/vkui';

const HowItWorks = ({ go, id }) => {

    return (
        <Panel id={id} separator={false}>
            <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
                Как работает ДУРКА
                </PanelHeaderSimple>
            <Div>
                <Group header={<Header mode="secondary">Как майнить DurkaCoins?</Header>}>
                    <Cell>
                        Первый способ:
                        Купите майнер в магазине и после успешной покупки, 
                        майнинг автоматически начнется.
                        Второй способ:
                        В группе нашего сообщества
                        <Link href="https://vk.com/durkacoin" target="_blank">
                             {` ДУРКА `}
                         </Link>
                        кидайте в "предложить новость" свои оригинальные мемы 
                        с ДУРКОЙ (баяны и взятые из других групп автоматически удаляются)
                        и получайте за каждый опубликованный +10 DurkaCoins.
                        Подписывайтесь на наше сообщество и следите за новостями на стене 
                        <Link href="https://vk.com/durkacoin" target="_blank">
                        {` ДУРКИ `}
                         </Link>
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Стоимость DurkaCoins</Header>}>
                    <Cell>
                        Приглашай друзей, повышай курс DurkaCoins.
                        DurkaCoin очень тяжело майнится, поэтому его стоимость
                        зависит от кол-ва подписчиков в <Link href="https://vk.com/durkacoin" target="_blank">
                             {` ДУРКЕ `}
                         </Link>, соответственно каждый из Вас может повлиять 
                         на повышение его курса позвав своих друзей.
                         При каждой новой 1000 подписчиков, стиомость DurkaCoins
                         вырастает на 0,01 руб.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Биржа</Header>}>
                    <Cell>
                        На главном экране нажмите кнопку "Биржа",
                        после чего у Вас повится 2 вкладки: Купить/Продать.
                        Если хотите купить коины, то сперва пополните баланс в рублях
                        нажав кнопку "Пополнить" и затем можете покупать DurkaCoins.
                        Чтобы продать свои DurkaCoins, нужно перейти во вкладку 
                        "Продать" и заполнить необходимые поля, затем нажать кнопку
                        "Добавить предложение". Каждое предложение публикуется на 24 часа.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Как попасть в ТОП?</Header>}>
                    <Cell>
                        ТОП формируется на основе Вашего рейтинга.
                        Чем больше Ваш рейтинг, тем выше Вы в ТОП-е.
                        ТОП по сообществам формируется на основе 
                        рейтинга всех пользователей, которые перешли по 
                        ссылке из вашей группы. Чтобы добавить ДУРКУ к себе 
                        в группу, на главном экране нажмите кнопку "Действия"
                        затем кнопку "Добавить в группу", и выберите в какую
                        группу хотите добавить приложение.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Для чего нужен DurkaCoin?</Header>}>
                    <Cell>
                        DurkaCoin можно переводить любым пользователям
                        и тратить на покупки в "Магазине" и скоро можно
                        будет продать на бирже.
                        Для ускорения развития в ДУРКЕ, Вы можете купить
                        DurkaCoins за реальные деньги. Для этого,  
                        на главном экране нажмите кнопку "DurkaCoin", а 
                        затем "Купить".
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Где хранятся покупки из магазина?</Header>}>
                    <Cell>
                        На главном экране нажмите кнопку "Инвентарь"
                        и у Вас отобразятся все товары и майнеры, которые
                        вы купили.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Использование купленных товаров</Header>}>
                    <Cell>
                        Открываете свой инвентарь, выбираете товар, который
                        хотите использовать и нажиматете кнопку "Использовать".
                        Пилюли и отпуск можно использовать только на себе.
                        Смирительную рубашку и одиночную камеру можно использовать
                        на любом пользователе ДУРКИ.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">История операций</Header>}>
                    <Cell>
                        Чтобы посмотреть историю переводов, кому вы  
                        перевели или кто перевел вам DurkaCoin, нужно на главном 
                        экрана нажать кнопку "DurkaCoin" , а затем выбрать "История операций".
                    </Cell>
                </Group>
            </Div>
        </Panel>
    );
};

export { HowItWorks };
