import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll
} from "@vkontakte/vkui";

import { ShopItem, MiningItem, LootBoxItem, StickersItem } from "../components";

import { requestMethod, formatCoins, dataShopLoc, dataLootBox, dataStickers, dataMining } from "../helpers";

const Shop = ({
  id,
  openInfo,
  transfer,
  buyItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  durkaCoins,
  miners,
  openBuyMining,
  openInfoLootBox,
  openInfoStickers,
}) => {
  const [activeTab, setActiveTab] = useState("product");
  // const [localScheme, setLocalScheme] = useState("bright_light");

  // useEffect(() => {
  //   console.log("scheme", scheme);
  //   if (scheme) {
  //     setLocalScheme(scheme);
  //     console.log("localScheme", localScheme);

  //   }
  // }, [scheme]);

  function tabProduct() {
    setActiveTab("product");
    window.scrollTo(0, 0);
  }

  function tabMining() {
    setActiveTab("mining");
    window.scrollTo(0, 0);
  }

  function tabStickers() {
    setActiveTab("stickers");
    window.scrollTo(0, 0);
  }

  function tabLootBox() {
    setActiveTab("loot-box");
    window.scrollTo(0, 0);
  }

  const renderShop = dataShopLoc.map((item, index) => {
    return (
      <ShopItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        openInfo={openInfo}
        openBuyItem={openBuyItem}
        scheme={scheme}
      />
    );
  });

  const renderMining = dataMining.map((item, index) => {
    let currentLevelItem = item.levels[miners[item.id]];
    let localBuyItem = buyItem;

    if (miners[item.id] >= 5) {
      currentLevelItem = item.levels[miners[item.id] - 1];
      // currentLevelItem = "Максимальный уровень";
      localBuyItem = () => {};
    }

    let price = currentLevelItem.price / 100000;

    return (
      <MiningItem
        key={index}
        id={item.id}
        name={item.name}
        price={price}
        icon={item.icon}
        cps={currentLevelItem.cps}
        scheme={scheme}
        buyItem={localBuyItem}
        level={miners[item.id]}
        openBuyMining={openBuyMining}
      />
    );
  });

  const renderLootBox = dataLootBox.map((item, index) => {
    return (
      <LootBoxItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        DC={item.DC}
        pillBlue={item.pillBlue}
        pillRed={item.pillRed}
        pillGold={item.pillGold}
        vacation={item.vacation}
        straitjacket={item.straitjacket}
        singleСamera={item.singleСamera}
        openInfoLootBox={openInfoLootBox}
        openBuyMining={openBuyMining}
        scheme={scheme}
      />
    );
  });

  const renderStickers = dataStickers.map((item, index) => {
    return (
      <StickersItem
        key={index}
        id={item.id}
        name={item.name}
        price={item.price}
        icon={item.icon}
        amount={item.amount}
        openInfoStickers={openInfoStickers}
        openBuyMining={openBuyMining}
        openBuyItem={openBuyItem}
        scheme={scheme}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        Магазин
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
            <HorizontalScroll>
              <TabsItem onClick={tabProduct} selected={activeTab === "product"}>
                Товары
              </TabsItem>
              <TabsItem onClick={tabMining} selected={activeTab === "mining"}>
                Майнеры
              </TabsItem>
              <TabsItem
                onClick={tabStickers}
                selected={activeTab === "stickers"}
              >
                Стикеры
              </TabsItem>
              <TabsItem
                onClick={tabLootBox}
                selected={activeTab === "loot-box"}
              >
                Лутбоксы
              </TabsItem>
              
              </HorizontalScroll>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "Отсутствует интернет - соединение!",
              updateConnection,
              "Повторить"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "Ошибка подключения к серверу",
                  socketReconnect,
                  "Повторить"
                )
              ) : (
                <>
                  {activeTab === "product" ? (
                    <>
                      {renderShop.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderShop}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "mining" ? (
                    <>
                      {renderMining.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderMining}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "loot-box" ? (
                    <>
                      {renderLootBox.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderLootBox}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "stickers" ? (
                    <>
                      {renderStickers.length ? (
                        <Div style={{ marginTop: 55 }}>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "row",
                              marginLeft: 25,
                              marginBottom: 10,
                            }}
                          >
                            Баланс: {formatCoins(durkaCoins)}
                            <Div className="iconDC" style={{ marginLeft: 8 }} />
                          </Div>

                          {renderStickers}
                        </Div>
                      ) : (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      )}
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}
              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Shop };
