import React from 'react';
import { Cell, Div, Avatar, Link, Button } from '@vkontakte/vkui';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';
import styles from "../styles/custom-styles.sass";
import { getRankByRating } from '../helpers';
import Icon16Add from '@vkontakte/icons/dist/16/add';

const ListGroups = ({ idVk, id, name, rating, photoURL, addGroupTop }) => {
    const urlGroup = 'https://vk.com/club' + idVk;

    return (
        <Div className='card'>
            <span className='listNumber'>{id}</span>
            <Cell
                className='myItem'
                before={photoURL ?
                    <Link href={urlGroup} target="_blank" >
                        <Avatar src={photoURL} />
                    </Link>
                    :
                    <Avatar src={'https://vk.com/images/camera_200.png'} />}
                description={
                    <Div style={{ padding: 0, marginLeft: 10, display: 'flex', alignItems: 'start', flexDirection: 'row' }}>
                      
                        <Div className="iconStar" />
                     
                        <Div style={{ padding: 0, marginTop: 1}}>
                            {rating}
                        </Div>
                    </Div>
                }
                asideContent={
                    <Div
                        id={id}
                        onClick={addGroupTop}
                        className="div"
                    >
                        <Icon16Add fill="green" />
                    </Div>

                }
            >
                <Div className="div" style={{ marginLeft: 10 }}
                >
                    {name}
                </Div>

            </Cell>
            {/* </Link> */}
        </Div>
    );
};

export { ListGroups };

