import React from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
} from "@vkontakte/vkui";

const ModalIntuition = ({
  setActiveModal,
  onChangeAction,
  makeBet
}) => {


  return (
    <>
        <ModalRoot activeModal="modal-intuition">
          <ModalCard
            id="modal-intuition"
            onClose={() => setActiveModal(null)}
            header="Сделать ставку"
            caption={
              <>
                <FormLayout>
                  
                    <Input
                      top="Введите целое число от 0 до 300"
                      placeholder="1"
                      onChange={onChangeAction}
                      type="text"
                    />
                    <InfoRow>Ставка 5 DurkaCoins</InfoRow>
                    <InfoRow>Чем ближе ваше число к выпавшему, тем выше шанс выиграть</InfoRow>
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Сделать ставку",
                mode: "commerce",
                action: () => {
                  makeBet()
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
     
    </>
  );
};

export { ModalIntuition };
