import React, { useState, useEffect, useReducer } from "react";
import { ActionSheet, ActionSheetItem } from "@vkontakte/vkui";

//payment-method
import { qiwiTypeManager, vkPayTypeManager } from "../payment-method";
// import { SocketManager } from "../helpers";

// const { callMethod } = SocketManager.createConnection();

export function paymentManager(
  type,
  amount,
  course,
  vipNameColor,
  period,
  setActiveModal,
  openAlertPay,
  setPopout,
  callMethod
) {
  switch (type) {
    case "durkacoins":
      getLinkAnyPay(
        amount,
        type,
        period,
        vipNameColor,
        setActiveModal,
        openAlertPay,
        setPopout,
        course,
        callMethod
      );
      break;
    case "vip":
      getLinkAnyPay(
        { 1: 29, 7: 139, 30: 499 }[period],
        "vip",
        period,
        vipNameColor,
        setActiveModal,
        openAlertPay,
        setPopout,
        course,
        callMethod
      );
      break;
    case "gold":
      getLinkAnyPay(
        { 1: 199, 7: 1399, 30: 4999 }[period],
        "gold",
        period,
        vipNameColor,
        setActiveModal,
        openAlertPay,
        setPopout,
        course,
        callMethod
      );
      break;
    case "exchange":
      getLinkAnyPay(
        amount,
        type,
        period,
        vipNameColor,
        setActiveModal,
        openAlertPay,
        setPopout,
        course,
        callMethod
      );
      break;
    default:
      break;
  }
}

function getLinkAnyPay(
  _amount,
  _donateType,
  _period = null,
  _vipNameColor = null,
  setActiveModal,
  openAlertPay,
  setPopout,
  course,
  callMethod
) {
  return callMethod("getDonateLink", {
    amount: {
      durkacoins: Number((_amount * course).toFixed(2)),
      vip: _amount,
      gold: _amount,
      exchange: _amount,
    }[_donateType],
    donateType: _donateType, // vip, gold, durkacoins, exchange
    period: _period, // 1, 7, 30
    vipNameColor: _vipNameColor, // 1, 2, 3, 4
  }).then(({ status, message }) => {
    if (status === "success") {
      let needCoins = 0;

      while (needCoins * course < 1) {
        needCoins++;
      }

      setActiveModal(null);

      if (_donateType === "durkacoins" && _amount < needCoins) {
        openAlertPay(`Минимальное кол-во ${needCoins} DurkaCoins`, "red");
      } else {
        setPopout(
          <>
            <ActionSheet onClose={() => setPopout(null)}>
              <ActionSheetItem
                autoclose
                onClick={
                  // {
                  //   durkacoins: qiwiPay,
                  //   vip: qiwiPayVIP,
                  //   gold: qiwiPayGoldVIP,
                  //   exchange: exchangeQiwiPay,
                  // }[_donateType]

                  {
                    durkacoins: () =>
                      qiwiTypeManager(_donateType, _amount, course),
                    vip: () =>
                      qiwiTypeManager(
                        _donateType,
                        _amount,
                        course,
                        _period,
                        _vipNameColor,
                        setActiveModal,
                        openAlertPay
                      ),
                    gold: () =>
                      qiwiTypeManager(
                        _donateType,
                        _amount,
                        course,
                        _period,
                        _vipNameColor,
                        setActiveModal,
                        openAlertPay
                      ),
                    exchange: () =>
                      qiwiTypeManager(
                        _donateType,
                        _amount,
                        course,
                        _period,
                        _vipNameColor,
                        setActiveModal,
                        openAlertPay
                      ),
                  }[_donateType]
                }
              >
                Оплатить через QIWI
              </ActionSheetItem>
              <ActionSheetItem
                autoclose
                onClick={
                  //   {
                  //     durkacoins: payVkPay,
                  //     vip: payVkPayVIP,
                  //     gold: payVkPayGoldVIP,
                  //     exchange: payVkPayExchange,
                  //   }[_donateType]
                  // }
                  {
                    durkacoins: () =>
                      vkPayTypeManager(_donateType, _amount, course),
                    vip: () =>
                      vkPayTypeManager(
                        _donateType,
                        _amount,
                        course,
                        _period,
                        _vipNameColor,
                        setActiveModal,
                        openAlertPay
                      ),
                    gold: () =>
                      vkPayTypeManager(
                        _donateType,
                        _amount,
                        course,
                        _period,
                        _vipNameColor,
                        setActiveModal,
                        openAlertPay
                      ),
                    exchange: () =>
                      vkPayTypeManager(
                        _donateType,
                        _amount,
                        course,
                        _period,
                        _vipNameColor,
                        setActiveModal,
                        openAlertPay
                      ),
                  }[_donateType]
                }
              >
                Оплатить через VK Pay
              </ActionSheetItem>
              <ActionSheetItem
                autoclose
                href={message}
                target="_blank"
                onClick={() => {
                  setActiveModal(null);
                }}
              >
                <div>
                  <div>Другой способ оплаты</div>
                  <div style={{ fontSize: "12px", color: "#818C99" }}>
                    даже со счета мобильного
                  </div>
                </div>
              </ActionSheetItem>
            </ActionSheet>
          </>
        );
      }
      console.log("getDonateLink", message);
    } else {
      // openAlert(message, "red");
      console.log("getDonateLink else", message);
    }
  });
}

